interface ProfileDropdownProps {
  onClose: () => void;
  onNavigate: (path: string) => void;
  userSignId: string;
}

const ProfileDropdown = ({ onClose, onNavigate, userSignId }: ProfileDropdownProps) => {
  const handleLinkClick = (path: string) => {
    onNavigate(path);
    onClose();
  };

  return (
      <div className={styles.profileDropdown}>
        <div className={styles.dropdownHeader}>
          <strong>{userSignId}</strong>
        </div>
        <button onClick={() => handleLinkClick(`/my-posts/${userSignId}`)} className={styles.dropdownItem}>
          ë‚´ ê²Œì‹œë¬¼
        </button>
        <button onClick={() => handleLinkClick('/setting')} className={styles.dropdownItem}>
          ì„¤ì • (ì´ë¯¸ì§€/ë¹„ë°€ë²ˆí˜¸)
        </button>
      </div>
  );
};

export default function Topbar() {
  const router = useRouter();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showFriendModal, setShowFriendModal] = useState(false);
  const [profileImg, setProfileImg] = useState('');
  const [userSignId, setUserSignId] = useState('');
  const [imageLoadError, setImageLoadError] = useState(false);
  const [showProfileDropdown, setShowProfileDropdown] = useState(false);

  // âœ… [ìˆ˜ì •] ë¡œê·¸ì¸ ìƒíƒœ ë° í”„ë¡œí•„ ì •ë³´ë¥¼ ë‹¤ì‹œ ë¶ˆëŸ¬ì˜¤ëŠ” í•¨ìˆ˜ ì¶”ì¶œ
  const checkLoginStatus = () => {
    const token = localStorage.getItem("accessToken");
    const expiresAt = localStorage.getItem("accessTokenExpiresAt");
    const userId = localStorage.getItem("userSignId");
    const profile = localStorage.getItem("profile_img"); // ì„¤ì • í˜ì´ì§€ì™€ í‚¤ ê°’ í™•ì¸ í•„ìš”

    if (!token || !expiresAt) {
      setIsLoggedIn(false);
      return;
    }

    if (Date.now() > Number(expiresAt)) {
      forceLogout();
      return;
    }

    setIsLoggedIn(true);
    setUserSignId(userId || '');
    setProfileImg(profile || '');
    // ìƒˆë¡œìš´ ì´ë¯¸ì§€ë¥¼ ë¶ˆëŸ¬ì˜¬ ë•Œ ì—ëŸ¬ ìƒíƒœ ì´ˆê¸°í™”
    setImageLoadError(false);
  };

  // âœ… [ì¶”ê°€] ì‹¤ì‹œê°„ í”„ë¡œí•„ ì—…ë°ì´íŠ¸ë¥¼ ìœ„í•œ ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ë“±ë¡
  useEffect(() => {
    checkLoginStatus();

    // 'profileUpdate' ì»¤ìŠ¤í…€ ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ë“±ë¡
    const handleProfileUpdate = () => {
      console.log("ğŸ”„ í”„ë¡œí•„ ë³€ê²½ ê°ì§€: Topbar ì—…ë°ì´íŠ¸");
      checkLoginStatus();
    };

    window.addEventListener('profileUpdate', handleProfileUpdate);

    return () => {
      window.removeEventListener('profileUpdate', handleProfileUpdate);
    };
  }, []);

  // ìë™ ë¡œê·¸ì•„ì›ƒ ë¡œì§
  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    const expiresAt = localStorage.getItem("accessTokenExpiresAt");
    if (!token || !expiresAt) return;

    const remainingTime = Number(expiresAt) - Date.now();
    if (remainingTime <= 0) {
      forceLogout();
      return;
    }

    const timer = setTimeout(() => {
      forceLogout();
    }, remainingTime);

    return () => clearTimeout(timer);
  }, []);

  const handleLoginSuccess = () => {
    checkLoginStatus();
    setShowLoginModal(false);
  };

  const handleLogout = () => {
    localStorage.clear();
    setIsLoggedIn(false);
    setProfileImg('');
    setUserSignId('');
    alert("ë¡œê·¸ì•„ì›ƒ ë˜ì—ˆìŠµë‹ˆë‹¤.");
    router.push("/");
  };

  const forceLogout = () => {
    console.warn("â³ í† í° ë§Œë£Œ â†’ ìë™ ë¡œê·¸ì•„ì›ƒ");
    localStorage.clear();
    setIsLoggedIn(false);
    setProfileImg('');
    setUserSignId('');
    setShowProfileDropdown(false);
    alert("ë¡œê·¸ì¸ ì‹œê°„ì´ ë§Œë£Œë˜ì—ˆìŠµë‹ˆë‹¤.");
    router.replace("/");
  };

  const handleProfileClick = () => {
    setShowProfileDropdown(prev => !prev);
  };

  const handleNavigate = (path: string) => {
    router.push(path);
    setShowProfileDropdown(false);
  };

  const currentUserSignId = typeof window !== "undefined"
      ? localStorage.getItem("userSignId") || ""
      : "";

  return (
      <>
        <header className={styles.topbar}>
          <Link href="/" className={styles.leftSection}>
            MomenTory
          </Link>

          <nav className={styles.rightSection}>
            {isLoggedIn ? (
                <>
                  <Link href="/community" className={`${styles.rightItem} ${styles.navLink}`}>
                    ì»¤ë®¤ë‹ˆí‹°
                  </Link>

                  <div
                      className={styles.rightItem}
                      style={{ cursor: 'pointer' }}
                      onClick={() => setShowFriendModal(true)}
                  >
                    ì¹œêµ¬
                    {showFriendModal && currentUserSignId && (
                        <FriendActionModal
                            currentUserSignId={currentUserSignId}
                            isOpen={showFriendModal}
                            onClose={() => setShowFriendModal(false)}
                        />
                    )}
                  </div>

                  <Link href="/page" className={`${styles.rightItem} ${styles.writeButton}`}>
                    ì±„íŒ…
                  </Link>

                  <Link href="/write" className={`${styles.rightItem} ${styles.writeButton}`}>
                    Log ì‘ì„±
                  </Link>

                  <div className={styles.profileContainer}>
                    <div
                        className={styles.profileSection}
                        onClick={handleProfileClick}
                        style={{ cursor: 'pointer' }}
                    >
                      {profileImg && !imageLoadError ? (
                          <img
                              src={getImageUrl(profileImg)}
                              alt="í”„ë¡œí•„"
                              className={styles.profileImage}
                              onError={(e) => {
                                setImageLoadError(true);
                                e.currentTarget.style.display = 'none';
                              }}
                          />
                      ) : (
                          <div className={styles.profileCircle}>
                            {getInitial(userSignId)}
                          </div>
                      )}
                    </div>

                    {showProfileDropdown && (
                        <ProfileDropdown
                            onClose={() => setShowProfileDropdown(false)}
                            onNavigate={handleNavigate}
                            userSignId={userSignId}
                        />
                    )}
                  </div>

                  <div className={styles.rightItem} onClick={handleLogout} style={{ cursor: 'pointer' }}>
                    ë¡œê·¸ì•„ì›ƒ
                  </div>
                </>
            ) : (
                <div
                    className={styles.rightItem}
                    style={{ cursor: "pointer" }}
                    onClick={() => setShowLoginModal(true)}
                >
                  ë¡œê·¸ì¸
                </div>
            )}
          </nav>
        </header>

        {showLoginModal && (
            <LoginModal
                onClose={() => setShowLoginModal(false)}
                onLoginSuccess={handleLoginSuccess}
            />
        )}
      </>
  );
}